___CSS_LOADER_EXPORT___.push([module.id, `body {
  max-width: 1200px; }

.wrapper {
  position: relative;
  background-color: aliceblue;
  display: grid;
  grid-template-columns: 1fr 3fr; }

.main {
  background-color: aqua; }

.add-new {
  position: absolute;
  bottom: 1rem;
  right: 1rem;
  cursor: pointer; }

svg {
  width: 35px;
  aspect-ratio: 1; }

.side-bar {
  display: flex;
  flex-direction: column;
  font-size: 1rem;
  font-weight: 600;
  min-height: 100vh; }
  .side-bar .profile {
    display: flex;
    align-items: center;
    gap: 1rem;
    padding: 1rem;
    background-color: aquamarine; }
    .side-bar .profile .profile-picture {
      width: 50px;
      aspect-ratio: 1;
      background-color: beige;
      border-radius: 100vh; }
    .side-bar .profile .profile-name {
      font-size: 1.3rem;
      font-weight: 600; }
  .side-bar ul {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 1rem 1rem; }
    .side-bar ul li {
      display: flex;
      align-items: center;
      gap: .5rem;
      cursor: pointer; }
  .side-bar .projects {
    padding: 1rem; }
    .side-bar .projects p {
      font-size: 1.2rem;
      opacity: .5; }
