import './css/reset.scss';
import './css/index.scss';
import './css/createBox.scss';
import './css/createTodo.scss';

import { CreateTodo } from './js/create.js';
import { todoCreationForm, addListBox, checkPrio} from './js/uiCreateTodo.js';
import { box } from './js/uiCreateBox.js';
window.addListBox = addListBox; // Add addListBox to the global scope


let addBtn = document.querySelector('.add-new');
let createToDo = document.querySelector('.todoCreationForm');
let createList;

addBtn.addEventListener('click', () => {
    document.querySelector('.wrapper').appendChild(box);
    document.querySelector('.box-menu').appendChild(todoCreationForm);
})

todoCreationForm.addEventListener('submit', (e) => {
    e.preventDefault();
    function prio() {
        let prio;
        let prioElements = document.querySelectorAll('.prio');
        prioElements.forEach(e => {
            e.addEventListener('click', (e) => {
                
                console.log('Priority:', prio);
            });
        });
    }

    CreateTodo(document.querySelector('.title'),
        document.querySelector('.description'),
        document.querySelector('#dateInput').value,
        prio,
        document.querySelector('textarea').value
    )
});
